@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@300;500&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
  font-family: 'MuseoModerno', cursive;
  font-weight: 300;
}

h1 {
  font-size: 170%;
}

h2 {
  font-size: 130%;
}

h1, h2 {
  font-weight: bold;
}

section {
  padding: 50px 0;
}

.container {
  width: 96%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: 720px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 960px;
  }
}

.nav {
  background-color: black;
  height: 80px;
  color: white;
}

.nav .container {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav a {
  font-size: 130%;
  font-weight: 500;
  text-decoration: none;
  color: white;
}

.nav__logo {
  width: 140px;
}

.nav__search input {
  height: 30px;
  border: none;
  border-radius: 8px;
}

.nav__search button {
  height: 30px;
  padding: 5px;
  border-radius: 8px;
}

.nav__search input, .nav__search button {
  height: 30px;
  padding: 3px 5px;
}
.featured__hero {
  position: relative;
  width: 100%;
  border-radius: 20px;
  box-shadow: 0 3px 7px rgba(0,0,0,0.25);
  transition: transform 0.5s ease-in-out;
}

@media (min-width: 768px) {
  .featured__hero {
    width: 49%;
  }
}

@media (min-width: 1200px) {
  .featured__hero {
    width: 32%;
    cursor: pointer;
  }

  .featured__hero:after {
    content: "";
    border-radius: 20px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 5px 15px rgba(0,0,0,0.3);
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
  }

  .featured__hero:hover {
    transform: scale(1.1);
  }

  .featured__hero:hover::after {
    opacity: 1;
  }
}

.featured__hero h2 {
  margin: 10px 0;
  text-align: center;
}

.featured__hero__img {
  position: relative;
  display: block;
  width: 95%;
  margin: 0 auto;
  border-radius: 20px;
}

.featured__hero__stats {
  display: flex;
  justify-content: space-around;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  padding: 15px 0 15px 0;
  text-align: center;
  background-color: white;
}

.featured__hero__stats_icon {
  width: 25px;
  margin-bottom: 10px;
}

.featured__hero__stats > div {

}
.featured {
  margin: 30px 0;
}

.featured h1 {
  margin-bottom: 25px;
}

.featured__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.loader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
}
.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid black;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}


